/* =============================================================================
   ANTIBOT ANIMATION SYSTEM
   Premium micro-interactions and smooth animations
   ============================================================================= */

:root {
    /* Spring Easings */
    --ease-spring: cubic-bezier(0.34, 1.56, 0.64, 1);
    --ease-bounce: cubic-bezier(0.68, -0.55, 0.265, 1.55);
    --ease-smooth: cubic-bezier(0.4, 0, 0.2, 1);
    --ease-out-expo: cubic-bezier(0.19, 1, 0.22, 1);
    --ease-in-out-back: cubic-bezier(0.68, -0.6, 0.32, 1.6);

    /* Stagger Delays */
    --stagger-1: 0ms;
    --stagger-2: 50ms;
    --stagger-3: 100ms;
    --stagger-4: 150ms;
    --stagger-5: 200ms;
    --stagger-6: 250ms;
}

/* =============================================================================
   ENTRANCE ANIMATIONS
   ============================================================================= */

/* Fade in with upward movement */
@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* Fade in with blur dissolve */
@keyframes fadeInBlur {
    from {
        opacity: 0;
        filter: blur(10px);
        transform: translateY(20px);
    }

    to {
        opacity: 1;
        filter: blur(0);
        transform: translateY(0);
    }
}

/* Fade in with scale */
@keyframes fadeInScale {
    from {
        opacity: 0;
        transform: scale(0.95);
    }

    to {
        opacity: 1;
        transform: scale(1);
    }
}

/* Slide from left */
@keyframes slideInLeft {
    from {
        opacity: 0;
        transform: translateX(-20px);
    }

    to {
        opacity: 1;
        transform: translateX(0);
    }
}

/* Slide from right */
@keyframes slideInRight {
    from {
        opacity: 0;
        transform: translateX(20px);
    }

    to {
        opacity: 1;
        transform: translateX(0);
    }
}

/* Row entrance for tables */
@keyframes rowSlideIn {
    from {
        opacity: 0;
        transform: translateX(-10px);
    }

    to {
        opacity: 1;
        transform: translateX(0);
    }
}

/* Modal entrance */
@keyframes modalIn {
    from {
        opacity: 0;
        transform: scale(0.9) translateY(-10px);
    }

    to {
        opacity: 1;
        transform: scale(1) translateY(0);
    }
}

/* Toast slide in */
@keyframes toastSlideIn {
    0% {
        opacity: 0;
        transform: translateX(100%) scale(0.8);
    }

    60% {
        transform: translateX(-5%) scale(1.02);
    }

    100% {
        opacity: 1;
        transform: translateX(0) scale(1);
    }
}

/* Toast slide out */
@keyframes toastSlideOut {
    from {
        opacity: 1;
        transform: translateX(0);
    }

    to {
        opacity: 0;
        transform: translateX(100%);
    }
}

/* =============================================================================
   MICRO-INTERACTION ANIMATIONS
   ============================================================================= */

/* Soft pulse for status indicators */
@keyframes pulseSoft {

    0%,
    100% {
        opacity: 1;
        transform: scale(1);
    }

    50% {
        opacity: 0.7;
        transform: scale(1.05);
    }
}

/* Button press feedback */
@keyframes buttonPress {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(0.97);
    }

    100% {
        transform: scale(1);
    }
}

/* Ripple effect */
@keyframes ripple {
    to {
        transform: scale(4);
        opacity: 0;
    }
}

/* Counter pop animation */
@keyframes countPop {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(1.15);
    }

    100% {
        transform: scale(1);
    }
}

/* Shake animation for errors */
@keyframes shake {

    0%,
    100% {
        transform: translateX(0);
    }

    20% {
        transform: translateX(-6px);
    }

    40% {
        transform: translateX(6px);
    }

    60% {
        transform: translateX(-4px);
    }

    80% {
        transform: translateX(4px);
    }
}

/* Bounce animation */
@keyframes bounce {

    0%,
    100% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-5px);
    }
}

/* Glow pulse for selected items */
@keyframes glowPulse {

    0%,
    100% {
        box-shadow: 0 0 20px rgba(88, 101, 242, 0.3);
    }

    50% {
        box-shadow: 0 0 35px rgba(88, 101, 242, 0.5);
    }
}

/* Icon check animation */
@keyframes checkBounce {
    0% {
        transform: scale(0);
        opacity: 0;
    }

    50% {
        transform: scale(1.2);
    }

    100% {
        transform: scale(1);
        opacity: 1;
    }
}

/* =============================================================================
   LOADING ANIMATIONS
   ============================================================================= */

/* Shimmer effect for loading states */
@keyframes shimmer {
    0% {
        background-position: -200% 0;
    }

    100% {
        background-position: 200% 0;
    }
}

/* Progress bar gradient animation */
@keyframes progressFlow {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}

/* Spinner with glow */
@keyframes spinGlow {
    0% {
        transform: rotate(0deg);
        filter: drop-shadow(0 0 5px rgba(88, 101, 242, 0.5));
    }

    50% {
        filter: drop-shadow(0 0 15px rgba(88, 101, 242, 0.8));
    }

    100% {
        transform: rotate(360deg);
        filter: drop-shadow(0 0 5px rgba(88, 101, 242, 0.5));
    }
}

/* =============================================================================
   BACKGROUND ANIMATIONS
   ============================================================================= */

/* Floating particles */
@keyframes float {

    0%,
    100% {
        transform: translateY(0) translateX(0);
    }

    25% {
        transform: translateY(-10px) translateX(5px);
    }

    50% {
        transform: translateY(-5px) translateX(-5px);
    }

    75% {
        transform: translateY(-15px) translateX(3px);
    }
}

/* Ambient orb movement */
@keyframes orbFloat {

    0%,
    100% {
        transform: translate(0, 0) scale(1);
    }

    33% {
        transform: translate(30px, -30px) scale(1.1);
    }

    66% {
        transform: translate(-20px, 20px) scale(0.9);
    }
}

/* Gradient shift */
@keyframes gradientShift {

    0%,
    100% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }
}

/* =============================================================================
   BUTTON EFFECTS
   ============================================================================= */

/* Shine sweep effect */
@keyframes shineSweep {
    0% {
        left: -100%;
    }

    100% {
        left: 100%;
    }
}

/* Border glow animation */
@keyframes borderGlow {

    0%,
    100% {
        border-color: rgba(88, 101, 242, 0.3);
    }

    50% {
        border-color: rgba(88, 101, 242, 0.6);
    }
}

/* =============================================================================
   UTILITY CLASSES
   ============================================================================= */

.animate-fade-in-up {
    animation: fadeInUp 0.5s var(--ease-smooth) forwards;
}

.animate-fade-in-blur {
    animation: fadeInBlur 0.6s var(--ease-smooth) forwards;
}

.animate-fade-in-scale {
    animation: fadeInScale 0.4s var(--ease-spring) forwards;
}

.animate-slide-in-left {
    animation: slideInLeft 0.4s var(--ease-smooth) forwards;
}

.animate-slide-in-right {
    animation: slideInRight 0.4s var(--ease-smooth) forwards;
}

.animate-shake {
    animation: shake 0.4s var(--ease-smooth);
}

.animate-bounce {
    animation: bounce 0.5s var(--ease-spring);
}

/* Stagger delay utilities */
.stagger-1 {
    animation-delay: var(--stagger-1);
}

.stagger-2 {
    animation-delay: var(--stagger-2);
}

.stagger-3 {
    animation-delay: var(--stagger-3);
}

.stagger-4 {
    animation-delay: var(--stagger-4);
}

.stagger-5 {
    animation-delay: var(--stagger-5);
}

.stagger-6 {
    animation-delay: var(--stagger-6);
}

/* Initially hidden for staggered animations */
.animate-stagger-item {
    opacity: 0;
}

/* =============================================================================
   PREMIUM NEON EFFECTS
   ============================================================================= */

/* Neon glow pulse - for accent elements */
@keyframes neonPulse {

    0%,
    100% {
        filter: drop-shadow(0 0 5px rgba(0, 212, 255, 0.5));
    }

    50% {
        filter: drop-shadow(0 0 20px rgba(0, 212, 255, 0.8)) drop-shadow(0 0 40px rgba(168, 85, 247, 0.5));
    }
}

/* Multi-color neon glow cycle */
@keyframes neonCycle {

    0%,
    100% {
        box-shadow: 0 0 20px rgba(0, 212, 255, 0.5);
    }

    33% {
        box-shadow: 0 0 20px rgba(168, 85, 247, 0.5);
    }

    66% {
        box-shadow: 0 0 20px rgba(236, 72, 153, 0.5);
    }
}

/* Floating animation - for cards/icons */
@keyframes floatUp {

    0%,
    100% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-10px);
    }
}

/* Gentle floating with rotation */
@keyframes floatRotate {

    0%,
    100% {
        transform: translateY(0) rotate(0deg);
    }

    25% {
        transform: translateY(-5px) rotate(1deg);
    }

    50% {
        transform: translateY(-10px) rotate(0deg);
    }

    75% {
        transform: translateY(-5px) rotate(-1deg);
    }
}

/* Border gradient travel animation */
@keyframes borderTravel {
    0% {
        background-position: 0% 50%;
    }

    100% {
        background-position: 200% 50%;
    }
}

/* Typewriter cursor blink */
@keyframes cursorBlink {

    0%,
    50% {
        border-color: transparent;
    }

    51%,
    100% {
        border-color: currentColor;
    }
}

/* Scale pop for numbers/stats */
@keyframes scalePop {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(1.15);
    }

    100% {
        transform: scale(1);
    }
}

/* Rainbow border animation */
@keyframes rainbowBorder {
    0% {
        border-color: rgba(0, 212, 255, 0.5);
    }

    25% {
        border-color: rgba(88, 101, 242, 0.5);
    }

    50% {
        border-color: rgba(168, 85, 247, 0.5);
    }

    75% {
        border-color: rgba(236, 72, 153, 0.5);
    }

    100% {
        border-color: rgba(0, 212, 255, 0.5);
    }
}

/* =============================================================================
   NEW UTILITY CLASSES
   ============================================================================= */

.animate-neon-pulse {
    animation: neonPulse 2s ease-in-out infinite;
}

.animate-neon-cycle {
    animation: neonCycle 3s ease-in-out infinite;
}

.animate-float {
    animation: floatUp 3s ease-in-out infinite;
}

.animate-float-rotate {
    animation: floatRotate 4s ease-in-out infinite;
}

.animate-border-travel {
    background-size: 200% 100%;
    animation: borderTravel 3s linear infinite;
}

.animate-scale-pop {
    animation: scalePop 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
}

.animate-rainbow-border {
    animation: rainbowBorder 4s ease infinite;
}

/* Hover-triggered animations */
.hover-float:hover {
    animation: floatUp 0.4s ease;
}

.hover-glow:hover {
    box-shadow: 0 0 30px rgba(88, 101, 242, 0.4);
}

.hover-neon:hover {
    box-shadow:
        0 0 20px rgba(0, 212, 255, 0.4),
        0 0 40px rgba(168, 85, 247, 0.3);
}